#menu-hamburger {
	display: none;
}

.menu > ul {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
}

.menu > ul > li {
	list-style-type: none;
	padding-top: 20px;
}

.menu > ul > li > a {
	color: #eb6f92;
	padding-right: 20px;
	transition: 0.3s;
}

.menu > ul > li > a > span {
	color: #ebbcba;
}

.menu > ul > li > a:hover {
	color: #ebbcba;
	font-size: 18px;
	transition: 0.3s;
}

#resume > a {
	color: #eb6f92;
	font-size: 16px;
	margin: 20px 20px 0px 0px;
	padding: 10px 10px 10px 10px;
	border: 1px solid #eb6f92;
	border-radius: 4px;
	transition: 0.3s;
}

#resume > a:hover {
	background-color: #2d324a;
	transition: 0.3s;
}

/* For smarphones and tablets */
@media only screen and (max-width: 810px) {
	#menu-desktop {
		display: none;
	}
	
	#menu-hamburger {
		display: block;
	}

	label .menu-hamburger {
		position: absolute;
		right: -100px;
		top: -100px;
		z-index: 100;
		width: 200px;
		height: 200px;
		background: #eb6f92;
		border-radius: 50% 50% 50% 50%;
		transition: .5s ease-in-out;
		box-shadow: 0 0 0 0 #ebbcba, 0 0 0 0 #ebbcba;
		cursor: pointer;
	}

	label .hamburger {
		position: absolute;
		top: 135px;
		left: 50px;
		width: 30px;
		height: 2px;
		background: #ebbcba;
		display: block;
		-webkit-transform-origin: center;
		transform-origin: center;
		transition: .5s ease-in-out;
	}

	label .hamburger:after, label .hamburger:before {
		transition: .5s ease-in-out;
		content: "";
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		background: #ebbcba;
	}

	label .hamburger:before { top: -10px; }

	label .hamburger:after { bottom: -10px; }

	label input { display: none; }

	label input:checked + .menu-hamburger {
		box-shadow: 0 0 0 100vw #eb6f92, 0 0 0 100vh #eb6f92;
		border-radius: 0;
	}

	label input:checked + .menu-hamburger .hamburger {
		-webkit-transform: rotate(45deg);
		transform: rotate(45deg);
	}

	label input:checked + .menu-hamburger .hamburger:after {
		-webkit-transform: rotate(90deg);
		transform: rotate(90deg);
		bottom: 0;
	}

	label input:checked + .menu-hamburger .hamburger:before {
		-webkit-transform: rotate(90deg);
		transform: rotate(90deg);
		top: 0;
	}

	label input:checked + .menu-hamburger + ul { opacity: 1; }

	label ul {
		z-index: 200;
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
		opacity: 0;
		transition: .25s 0s ease-in-out;
	}

	label li {
		list-style-type: none;
	}

	label a {
		margin-bottom: 1em;
		display: block;
		color: #24283b;
		text-decoration: none;
	}

	label a:hover {
		font-size: 20px;
		font-weight: bold;
	}
}

#animated {
	opacity: 0;
	transition: opacity 1.2s ease-in-out;
}

main {
	height: 100vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	font-size: 50px;
}

/* For smartphones and tablets */
@media only screen and (max-width: 810px) {
	main {
		font-size: 20px;
	}
}

#loader {
	position: absolute;
	width: 600px;
	height: 36px;
	left:50%;
	top:40%;
	margin-left: -300px;
	overflow: visible;
	-webkit-user-select: none;
	user-select: none;
	cursor: default;
}

#loader > div {
	position: absolute;
	width: 50px;
	height: 36px;
	opacity: 0;
	animation: move 5s linear;
	-o-animation: move 5s linear;
	-moz-animation: move 5s linear;
	-webkit-animation: move 5s linear;
	transform: rotate(180deg);
	-o-transform: rotate(180deg);
	-moz-transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
	color: #eb6f92;
}

#loader > div:nth-child(2) {
	animation-delay: 0.2s;
	-o-animation-delay: 0.2s;
	-moz-animation-delay: 0.2s;
	-webkit-animation-delay: 0.2s;
}

#loader > div:nth-child(3) {
	animation-delay: 0.4s;
	-o-animation-delay: 0.4s;
	-webkit-animation-delay: 0.4s;
	-webkit-animation-delay: 0.4s;
}

#loader > div:nth-child(4) {
	animation-delay: 0.6s;
	-o-animation-delay: 0.6s;
	-moz-animation-delay: 0.6s;
	-webkit-animation-delay: 0.6s;
}

#loader > div:nth-child(5) {
	animation-delay: 0.8s;
	-o-animation-delay: 0.8s;
	-moz-animation-delay: 0.8s;
	-webkit-animation-delay: 0.8s;
}

#loader > div:nth-child(6) {
	animation-delay: 1s;
	-o-animation-delay: 1s;
	-moz-animation-delay: 1s;
	-webkit-animation-delay: 1s;
}

#loader > div:nth-child(7) {
	animation-delay: 1.2s;
	-o-animation-delay: 1.2s;
	-moz-animation-delay: 1.2s;
	-webkit-animation-delay: 1.2s;
}

@keyframes move {
	0% {
		left: 0;
		opacity: 0;
	}
	35% {
		left: 41%;
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
		opacity: 1;
	}
	65% {
		left: 59%; 
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg); 
		opacity: 1;
	}
	100% {
		left: 100%; 
		-webkit-transform: rotate(-180deg); 
		transform: rotate(-180deg);
		opacity: 0;
	}
}

@-webkit-keyframes move {
	0% {
		left: 0; 
		opacity: 0;
	}
	35% {
		left: 41%; 
		-webkit-transform: rotate(0deg); 
		transform: rotate(0deg); 
		opacity: 1;
	}
	65% {
		left: 59%; 
		-webkit-transform: rotate(0deg); 
		transform: rotate(0deg); 
		opacity: 1;
	}
	100% {
		left: 100%;
		-webkit-transform: rotate(-180deg); 
		transform: rotate(-180deg); 
		opacity: 0;
	}
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body, html {
	font-family: Hack, 'Open Sans', sans-serif;
	color: #cfc9c2;
	background-color: #24283b;
	font-size: 16px;
	overflow-x: hidden;
}

a {
	text-decoration: none;
}

